.flip-card {
  background-color: transparent;
  width: 7rem;
  height: 2.5rem;
  perspective: 1000px;
  margin: 0.5rem 1rem;
  cursor: default;
}

/* This container is needed to position the front and back side */
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(153 153 153 / 17%);
  border-radius: 5px;
  color: #b8b8b8;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.flip-card-back {
  transform: rotateY(180deg);
}

